<template>
  <div class="slide_editor">
    <div class="params_area border-bottom">
      <div class="row px-3">
        <div class="col-3">
          <label
              for="slideTypeSelect"
              class="form-label"
          >
             
          </label>
          <select class="form-select"
                  id="slideTypeSelect"
                  :value="activeSlide.type"
                  @change="setSlideType($event.target.value)"
          >
            <option :value="-1" disabled>  </option>
            <option v-for="type in $store.state.slideTypes"
                    :key="type.id"
                    :value="type.id"
            >{{ type.name }}
            </option>
          </select>
        </div>

        <slot name="params"></slot>
      </div>
    </div>

    <div
        v-if="noControls"
        class="help_text"
    >   
    </div>

    <div
        v-else
        class="slide_controls_area"
    >
      <div class="slide_area">
        <RatioKeeper
            :ratio="16 / 9"
            :min-height="500"
        >
          <Slide/>
        </RatioKeeper>
      </div>

      <div class="controls_area border-start">
        <button
            class="controls_area__button"
            @click="addText"
        >
          <i class="fas fa-align-justify"></i>
          
        </button>

        <button
            class="controls_area__button"
            @click="addImage"
        >
          <i class="far fa-image"></i>
          
        </button>

        <button
            disabled
            class="controls_area__button"
            @click="addYouTube"
        >
          <i class="fab fa-youtube"></i>
          YouTube
        </button>

        <slot name="controls"></slot>

        <div style="flex: 1 1 auto"></div>

        <button
            class="controls_area__temp_button"
            @click="toggleScrollbars"
        >{{ $store.state.slideParams.isEntirelyVisible ? '' : '' }} 
        </button>

        <button
            class="controls_area__temp_button"
            @click="toggleGrid"
        >{{ $store.state.slideParams.showGrid ? '' : '' }} 
        </button>

        <button
            class="controls_area__temp_button"
            @click="logState"
        >Log vuex state
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import {mapActions, mapGetters, mapMutations} from "vuex";
import {ADD_TEXT_ELEMENT, ADD_IMAGE_ELEMENT, ADD_YOUTUBE_ELEMENT} from "@/store/actions/action-types";
import RatioKeeper from "@/components/utils/RatioKeeper";
import Slide from "@/components/Slide";
import {SET_SLIDE_TYPE, TOGGLE_GRID, TOGGLE_SCROLLBARS} from "@/store/mutations/mutation-types";

export default {
  name: 'SlideEditor',
  components: {Slide, RatioKeeper},
  props: {
    noControls: Boolean,
  },
  computed: {
    ...mapGetters(['activeSlide'])
  },
  methods: {
    ...mapMutations({
      setSlideType: SET_SLIDE_TYPE,
      toggleGrid: TOGGLE_GRID,
      toggleScrollbars: TOGGLE_SCROLLBARS,
    }),
    ...mapActions({
      // addText: ADD_TEXT_ELEMENT,
      // addImage: ADD_IMAGE_ELEMENT,
      addYouTube: ADD_YOUTUBE_ELEMENT,
    }),
    addText() {
      this.$store.dispatch(ADD_TEXT_ELEMENT, {x: 5, y: 5, width: 20, height: 10})
    },
    addImage() {
      this.$store.dispatch(ADD_IMAGE_ELEMENT, {x: 5, y: 5, width: 15, height: 10})
    },
    logState() {
      console.log(this.$store.state)
    },
  },
}
</script>

<style lang="scss">
@import "src/assets/styles/colors";

.slide_editor {
  display: flex;
  flex-direction: column;
  overflow: hidden;

  .params_area {
    height: 80px;

    .form-label {
      margin-top: .2rem;
      margin-bottom: .2rem;
    }
  }

  .help_text {
    flex: 1 1 auto;
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 0 5% 5%;

    font-size: 2rem;
    color: #8c8c8c;
  }

  .slide_controls_area {
    flex: 1 1 auto;
    overflow: hidden;
    display: flex;

    .slide_area {
      flex: 1 1 auto;
      overflow: auto;
      background-color: #f1f2f5;

      & > * > div.sidebar_item {
        box-shadow: 0 0 8px 2px rgba(0, 0, 0, 0.2);
      }
    }

    .controls_area {
      min-width: 110px;
      width: 110px;
      background-color: #f8f9fb;

      display: flex;
      flex-direction: column;

      .controls_area__temp_button {
        margin: 0 8px 8px;
      }

      .controls_area__button {
        margin: 8px 8px 0;

        &:first-child {
          margin-top: 12px;
        }

        border: none;
        background-color: transparent;
        border-radius: 4px;

        padding: 4px 8px;
        color: $primary;

        display: flex;
        flex-direction: column;
        align-items: center;

        i {
          font-size: 1.5rem;
          padding: 4px;
        }

        &:hover:enabled {
          background-color: rgba(0, 0, 0, 0.05);
        }

        &:active:enabled {
          background-color: rgba(0, 0, 0, 0.1);
        }

        &:disabled {
          opacity: .3;
        }
      }
    }
  }
}
</style>
