import {
    ADD_ANSWER_ACTION,
    ADD_ANSWERS_ELEMENT,
    ADD_IMAGE_ELEMENT,
    ADD_TEXT_ELEMENT, ADD_VALUABLE_ANSWER_ACTION, ADD_VALUABLE_ANSWERS_ELEMENT,
    ADD_YOUTUBE_ELEMENT, REMOVE_ANSWER_ACTION
} from "@/store/actions/action-types";
import {ADD_SLIDE_ELEMENT} from "@/store/mutations/mutation-types";

let answerIdCounter = 1

export default {
    [ADD_TEXT_ELEMENT]({commit}, params) {
        commit(ADD_SLIDE_ELEMENT, {
            ...params,
            content: {component: 'TextObject', value: ''},
        })
    },
    [ADD_IMAGE_ELEMENT]({commit}, params) {
        commit(ADD_SLIDE_ELEMENT, {
            ...params,
            content: {
                component: 'ImageObject', value: {
                    url: null,
                    fill: false,
                }
            },
        })
    },
    // eslint-disable-next-line no-unused-vars
    [ADD_YOUTUBE_ELEMENT]({commit}) {
        // commit(ADD_SLIDE_ELEMENT, {
        //     component: 'YouTubeObject',
        //     html: `<iframe src="https://www.youtube.com/embed/5P-ndTBfyeY?controls=0&autoplay=1" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>`
        // })
    },
    [ADD_ANSWER_ACTION]() {
        return new Promise((resolve) => {
            resolve({id: answerIdCounter++, text: ''})
        })
    },
    [ADD_VALUABLE_ANSWER_ACTION]() {
        return new Promise((resolve) => {
            resolve({id: answerIdCounter++, text: '', is_correct: false})
        })
    },
    // eslint-disable-next-line no-unused-vars
    [REMOVE_ANSWER_ACTION](answerId) {
        return new Promise((resolve) => {
            resolve()
        })
    },
    [ADD_ANSWERS_ELEMENT]({dispatch, commit}, params) {
        dispatch(ADD_ANSWER_ACTION).then(answer1 => {
            dispatch(ADD_ANSWER_ACTION).then(answer2 => {
                commit(ADD_SLIDE_ELEMENT, {
                    ...params,
                    content: {
                        component: 'AnswersObject', value: {
                            answers: [answer1, answer2],
                            columnsCount: 1,
                        }
                    },
                })
            })
        })
    },
    [ADD_VALUABLE_ANSWERS_ELEMENT]({dispatch, commit}, params) {
        dispatch(ADD_VALUABLE_ANSWER_ACTION).then(answer1 => {
            dispatch(ADD_VALUABLE_ANSWER_ACTION).then(answer2 => {
                commit(ADD_SLIDE_ELEMENT, {
                    ...params,
                    content: {
                        component: 'AnswersObject', value: {
                            answers: [answer1, answer2],
                            columnsCount: 1,
                        }
                    },
                })
            })
        })
    },
}